/* eslint-disable react-hooks/exhaustive-deps */
import React from "react";
import { getQuizListPaging } from "../../../api/actions";
import { Link } from "react-router-dom";
import { IconButton } from "@mui/material";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import SearchQuizBar from "./SearchQuizBar";

export default function QuizManageView(props) {

  const { categories } = props;
  const [candidates, setCandidates] = React.useState([]);
  //GET User - Candidate
  const getQuizListPage = (pageNumber) => {
    getQuizListPaging({page: `${pageNumber}`, limit: 100})
      .then((res) => {
        setCandidates(res.data.quizList)
      })
      .catch((err) => {
        console.log(err)
      })
  }
  React.useEffect(() => {
    getQuizListPage(1)
  }, [])

  // Search function (not completed yet)
  const [staffResult, setStaffResult] = React.useState(candidates); 
  const searchStaff = (values) => {
    const sName = values;
    if (sName !== "") {
      const result = candidates.filter((s) =>
        s.fullName.toLowerCase().match(sName.toLowerCase())
      );
      if (result.length > 0) {
        setStaffResult(result);
      } else {
        alert("No result!");
      }
    } else {
      setStaffResult([...candidates]);
    }
  };
  
  const searchResultRender = () => {
    if (staffResult.length > 0) {
      return staffResult;
    } else {
      return candidates;
    }
  };

  return (
    <div>
      <div>
        <div className="card__list-test">
          <div className="card__header">
            <h3 className="">Quản lý bài kiểm tra</h3>
          </div>
          <div>
            <SearchQuizBar 
              categories = {categories}
            />
            <div className="mt-3 text-left col-5">
              <div className="col-4 text-left add-staff-button">
                Tạo bài Test mới
                <Link to='/quiz/create/quiz/createquiz'>
                  <IconButton
                    sx={{ color: "rgba(255, 193, 69, 1)" }}
                  >
                    <AddCircleIcon />
                  </IconButton>
                </Link>
              </div>
            </div>
          </div>
        </div>
        <div className="my-4">
          <ListCandidateQuiz candidates={searchResultRender()} />
        </div>
      </div>
    </div>
  );
}
const ListCandidateQuiz = (props) => {
  const { candidates } = props
  const RenderCandidate = () => {
    if (candidates.length > 0) {
      return candidates
      .reduce((unique, item) => {
        return unique.map(item => item.description).includes(item.description) ? unique : [...unique, item]}, []
      )
        .map((can, idx) => (
          <tr key={idx} style={{ height: '60px', position: 'relative' }}>
            <td>{idx + 1}</td>
            <td>{can.description}</td>
            <td>{can.creator}</td>
            <td>{can.numberQuestions}</td>
            <td>{can.startTime}</td>
            <td>{can.expiredTime}</td>
            <td>{can.cate}</td>
            <td>
              <Link to={`/quiz/quiz-user/${can.id}`} style={styles.clickField} />
            </td>
          </tr>
        ));
    } else return null;
  };

  return (
    <table className="table card__table m-auto" style={{ width: "95%" }}>
      <thead className="">
        <tr>
          <th>STT</th>
          <th>Tên bài kiểm tra</th>
          <th>Người tạo</th> 
          <th>Số câu hỏi</th>
          <th>Thời gian mở</th>
          <th>Thời gian đóng</th>
          <th>Category</th>
        </tr>
      </thead>
      <tbody>
        <RenderCandidate />
      </tbody>
    </table>
  );
};

const styles = {
  clickField: {
    position: 'absolute',
    width: '100%',
    height: '60px',
    zIndex: 1000,
    top: 0,
    left: 0,
  }
}