import React from 'react'

export default function Icons(props) {
    const {
        type
    } = props
    
    const choice = () => {
        switch(type) {
            case 'success': 
                return '/icon/Ellipse_6.png'
            case 'warning':
                return '/icon/Vector_warning.png'
            default:
                return null
        }
    }
    return (
        <div>
            <img
                src={choice()}
                style={{ height: '95px', width: '95px', margin: '20px' }}
            />
        </div>
    )
}