import { colorChannel } from '@mui/system';
import React from 'react'
import { Puff } from "react-loader-spinner"
import SearchButton from '../../../helper/GroupButton/SearchButton'


export default function SearchQuizBar(props) {

    const [searchQuizInput, setSearchQuizInput] = React.useState({
            createDate: '',
            openTime: '',
            closeTime: '',
            kindOfQuestion:'',
            quizName:''
    });

    const {
        categories
    } = props

    const RenderOption = () => {
        if (categories.length > 0) {
            return categories.map((cate) => (
                <option key={cate.id} value={cate.id}>
                    {cate.name}
                </option>
            ))
        } else return null
    }

    const submitSearchQuiz = (e) => {
        e.preventDefault();
        console.log(searchQuizInput)
    };
    
    return (
        <div className='mt-4'>
            <form onSubmit={submitSearchQuiz}>
                <div className='row'>
                    <div className='col-2'>
                        <div className='form-group text-left'>
                            <label>Ngày tạo</label>
                            <input
                                className='form-control'
                                type='datetime-local'
                                value={searchQuizInput.createDate}
                                onChange={(e) => {
                                    setSearchQuizInput({
                                            ...searchQuizInput,
                                            createDate: 
                                                e.target.value,
                                    });
                                }}
                            />
                        </div>
                    </div>
                    <div className='col-2'>
                        <div className='form-group text-left'>
                            <label>Thời gian mở</label>
                            <input
                                className='form-control'
                                type='datetime-local'
                                value={searchQuizInput.openTime}
                                onChange={(e) => {
                                    setSearchQuizInput({
                                            ...searchQuizInput,
                                            openTime: 
                                                e.target.value,
                                    });
                                }}
                            />
                        </div>
                    </div>
                    <div className='col-2'>
                        <div className='form-group text-left'>
                            <label>Thời gian đóng</label>
                            <input
                                className='form-control'
                                type='datetime-local'
                                value={searchQuizInput.closeTime}
                                onChange={(e) => {
                                    setSearchQuizInput({
                                            ...searchQuizInput,
                                            closeTime: 
                                                e.target.value,
                                    });
                                }}
                            />
                        </div>
                    </div>
                    <div className='col-2'>
                        <div className='form-group text-left'>
                            <label>Category</label>
                            <select
                                className='form-control text-info form-select'
                                value={searchQuizInput.kindOfQuestion}
                                onChange={(e) => {
                                    setSearchQuizInput({
                                        ...searchQuizInput,
                                        kindOfQuestion: 
                                            e.target.value,
                                    });
                                }}
                            >
                                <option value='' disabled>
                                    select
                                </option>
                                <RenderOption />
                            </select>
                        </div>
                    </div>
                    <div className='col-2'>
                        <div className='form-group text-left'>
                            <label>Nội dung tìm kiếm</label>
                            <input
                                type='text'
                                className='form-control'
                                placeholder='Quiz name'
                                value={searchQuizInput.quizName}
                                onChange={(e) => {
                                    setSearchQuizInput({
                                        ...searchQuizInput,
                                            quizName:
                                                e.target.value,
                                    });
                                }}
                            />
                        </div>
                    </div>
                    <div className="col-1">
                        <SearchButton title={'Tìm kiếm'} type="submit" />
                    </div>
                </div>
            </form>
        </div>
    )
}