import React, { useState, useEffect } from 'react';
import {
    Grid,
    TextField,
    InputAdornment,
    Button,
    Typography,
} from '@material-ui/core';
import useStyle from './styles';

const InputSearchComponent = (props) => {
    const {
        handleSearch,
        title,
        placeholder,
        onChangeSearch,
        setWordSearch,
        handleChangeText,
        isSetOutSite,
        onBlur,
        defaultValue,
        value,
        name
    } = props;

    const [textSearch, setTextSearch] = useState('');
    const classes = useStyle();

    useEffect(() => {
        setTextSearch(value);
    }, [value]);



    return (
        <>
            <Typography style={{fontFamily: "Quicksand"}}>{title ?? ''}</Typography>
            <Grid style={{ display: 'flex', paddingTop:10 }} className={classes.container}>
                <TextField
                    onBlur={(val) => {
                        onBlur && onBlur(val);
                    }}
                    variant="outlined"
                    defaultValue={defaultValue}
                    placeholder={placeholder}
                    className={classes.container}
                    onChange={handleChangeText ? (e) => handleChangeText(name, e.target.value) : null}
                    value={textSearch}
                    InputProps={{
                        className: classes.search,
                    }}
                />
            </Grid>
        </>
    );
};

export default InputSearchComponent;
