/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { getQuestionsByQuizId } from '../../../api/actions'
import OverViewQuizComponent from '../../../components/Quiz/OverViewQuizComponent'

const TakeTest = () => {
    const history = useHistory()
    const [listQuiz, setListQuiz] = React.useState([])
    // const [id, setId] = React.useState('')
    // const [dataFrom, setDataFrom] = React.useState('')
    const [showResult, setShowResult] = React.useState(false)
    const toggleShowResult = () => {
        setShowResult(!showResult)
    }
    let id = ''
    let dataFrom = ''

    // GET quiz all by user id
    const getQuizWithUserId = () => {
        getQuestionsByQuizId(id)
            .then((res) => {
                setListQuiz(res.data)
            }).catch((err) => {
                console.error(err)
            })
    }
    if(history.location.state) {
        if(history.location.state.dataFrom === 'admin') {
            id = history.location.state.userId
            dataFrom = history.location.state.dataFrom
        }
    } else id = sessionStorage.getItem('id')

    useEffect(() => {
        getQuizWithUserId()
    },[])

    if (listQuiz.length > 0 && id !== '') {
    return (
        <OverViewQuizComponent
            listQuiz={listQuiz}
            showResult={showResult}
            toggleShowResult={toggleShowResult}
            dataFrom={dataFrom?dataFrom:''}
        />
    )
    } else
        return (
            <div className='page__out'>
                <div className='page__in'>
                    <div style={{ height: '80vh' }}>
                        <p className='text-info'>
                            <b>Hiện tại bạn chưa có bài Quiz nào cần làm!</b>
                        </p>
                    </div>
                </div>
            </div>
        );
};

export default TakeTest;
