import React from 'react';
import { Link } from 'react-router-dom'

let adminLinkTo = '/'

const StatusQuiz = (props) => {
    const { active } = props;
    return (
        <div
            className={
                active === 'done'
                    ? 'status-staff-active'
                    : 'status-staff-warning'
            }
            style={{ margin: 'auto' }}
        >
            <div style={{ padding: '2px' }}>
                {active === 'done' ? 'Đã làm' : 'Chưa làm'}
            </div>
        </div>
    );
}

const RenderQuizzes = (props) => {
    const {
        listQuiz,
        dataFrom
    } = props

    return listQuiz.map((quiz, index) => {
        // console.log(dataFrom)
        dataFrom ? (dataFrom !== 'admin' ? adminLinkTo = `/list-test/take-quiz/start/${quiz.id}` : adminLinkTo = `/list-test/take-quiz/finish/${quiz.id}`) : adminLinkTo = `/list-test/take-quiz/start/${quiz.id}`
        return (
            <tr key={index} style={{ height: '60px', position: 'relative' }}>
                <td className='text-center'>{index + 1}</td>
                <td className='text-center'>{quiz.description}</td>
                <td className='text-center'>{quiz.numberQuestions}</td>
                <td className='text-center'>{quiz.quizTime}</td>
                <td className='text-center'>{quiz.startTime}</td>
                <td className='text-center'>{quiz.expiredTime}</td>
                <td>
                    <StatusQuiz active={quiz.status} />
                </td>
                <td>
                    <Link
                        to={adminLinkTo}
                        style={{
                            height: '60px',
                            width: '100%', 
                            position: 'absolute', 
                            zIndex: 9, 
                            cursor: 'pointer', 
                            top: 0, 
                            left: 0 
                        }}
                    />
                </td>
            </tr>
        )
    })
}

export default function QuizTable(props) {
    const {
        listQuiz,
        dataFrom
    } = props;

    return (
        <table className="mt-3 card__table">
            <thead>
                <tr>
                    <th className='text-center'>STT</th>
                    <th className='text-center'>Tên bài kiểm tra</th>
                    <th className='text-center'>Số câu hỏi</th>
                    <th className='text-center'>Thời gian làm bài</th>
                    <th className='text-center'>Thời gian mở</th>
                    <th className='text-center'>Thời gian hết hạn</th>
                    <th className='text-center'>Trạng thái</th>
                </tr>
            </thead>
            <tbody className="">
                <RenderQuizzes
                    listQuiz={listQuiz}
                    dataFrom={dataFrom}
                />
            </tbody>
        </table>
    )
}

