import React from 'react';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import IconButton from '@mui/material/IconButton';
import { Link } from 'react-router-dom';
import {
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Typography,
} from '@mui/material';
import Paper from '@mui/material/Paper';

const StatusStaff = (props) => {
    const { active } = props;
    return (
        <div
            className={
                active === true
                    ? 'status-staff-active'
                    : 'status-staff-disabled'
            }
            style={{ margin: 'auto' }}
        >
            <div style={{ padding: '2px' }}>
                {active === true ? 'Active' : 'Disabled'}
            </div>
        </div>
    );
};

const RowStaff = (props) => {
    const { staffs } = props;

    return (
        <>
            {Array.isArray(staffs) && staffs.length > 0 && staffs?.map((item, index) => (
                    <TableRow key={index} style={{ position: 'relative' }}>
                        <TableCell align='center' component="th" scope="row">{index + 1}</TableCell>
                        <TableCell align="left">{item.fullName}</TableCell>
                        <TableCell align="left">{item.userType === 'Cand' ? 'Ứng viên' : item.userType}</TableCell>
                        <TableCell align="left">{item.email}</TableCell>
                        <TableCell align="left">{item.address}</TableCell>
                        <TableCell align="center">{item.company.shortCutName}</TableCell>
                        <TableCell align="left"><StatusStaff active={item.active} /></TableCell>
                        <Link className="text-dark" to={`/admin/staff-list/${item.id}`} style={styles.staffLinks}></Link>
                    </TableRow>
                ))}
        </>
    );
};
function TableData(props) {
    const { staffs } = props;
    return (
        <TableContainer component={Paper}>
            <Table sx={{ width: '100%' }} aria-label="simple table">
                <TableHead>
                    <TableRow>
                        <TableCell align="center">STT</TableCell>
                        <TableCell align="left">Họ và tên</TableCell>
                        <TableCell align="left">Vị trí</TableCell>
                        <TableCell align="left">Email</TableCell>
                        <TableCell align="left">Địa chỉ</TableCell>
                        <TableCell align="center">Công ty</TableCell>
                        <TableCell align="center">Trạng thái</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    <RowStaff staffs={staffs} />
                </TableBody>
            </Table>
        </TableContainer>
    );
}

export default TableData;

const styles = {
    staffLinks: {
        position: 'absolute',
        width: '100%',
        height: '100%',
        cursor: 'pointer',
        zIndex: 100,
        top: 0,
        left: 0,
    }
}